@tailwind base;
@tailwind components;
@tailwind utilities;
.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.navbar-custom {
	background-color: #650505;
}

.navbar-custom .navbar-brand {
	color: #fc9e9e;
}

.navbar-custom .navbar-nav .nav-link {
	color: #ffffff;
}

.navbar-custom .navbar-nav .nav-link:hover {
	color: #b0b0b0;
}

/* Buton Stili */
.btn-custom {
	background-color: #007bff;
	border-color: #007bff;
}

.btn-custom:hover {
	background-color: #0056b3;
	border-color: #0056b3;
}

/* Kart Stili */
.card-custom {
	background-color: #fff;
	border-radius: 5px;
	box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.card-custom-header {
	background-color: #f2f2f2;
	border-bottom: 1px solid #ddd;
}

.card-custom-body {
	padding: 20px;
}

/* Footer Stili */
.footer-custom {
	background-color: #333;
	color: #fff;
	padding: 20px 0;
}

.footer-custom p {
	margin-bottom: 0;
}

.centered-caption {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-10%, -50%);
}
.nav-link-spacing-left-items {
	margin-right: 30px;
}
.nav-link-spacing-right-items {
	margin-left: 30px;
}

:root {
	--bs-border-width: 0px; /* veya istediğiniz piksel değeri */
}
.relative-bg {
	background-image: url('./assets/images/hospital/bg.jpg');
	/* Diğer stillendirme özellikleri */
}